import React, { Component } from 'react'
import {
    Route,
    BrowserRouter,
    Link,
    Redirect,
    Switch
} from 'react-router-dom'
import 'purecss/build/menus.css'
import './index.css'
import { firebaseAuth } from '../data/config'
import logo from './media/logo.png'
import Home from './pages/'
import About from './pages/About'
import Error404 from './pages/Error404'
import Register from './pages/Register'
import Login from './pages/Login'
import DashboardCourses from './pages/protected/'
import { logout } from './helpers/Auth'

class App extends Component {
    constructor(...props){
        super(...props)

        this.state = {
            authed: false,
            loading: true
        }
    }

    handleOnCLick () {
        document.getElementById('tuckedMenu').classList.toggle('custom-menu-tucked')
        document.getElementById('toggle').classList.toggle('x')
    } 

    render(){
        const { loading } = this.state
        return(
            loading == false
            ?
                <h1>Cargando..</h1>
            :
                <BrowserRouter>
                    <div>
                    <header className="custom-menu-wrapper">
                        <div className="pure-menu custom-menu custom-menu-top">
                            <a href="#" className="pure-menu-heading custom-menu-brand">
                                <img className="logo" src={logo} alt="logo"/>
                            </a>
                            <a href="#" className="custom-menu-toggle" id="toggle" onClick={this.handleOnCLick}><s className="bar"></s><s className="bar"></s></a>
                        </div>
                        <div className="pure-menu pure-menu-horizontal pure-menu-scrollable custom-menu custom-menu-bottom custom-menu-tucked" id="tuckedMenu">
                            <div className="custom-menu-screen"></div>
                            <ul className="pure-menu-list">
                                <li className="pure-menu-item">
                                    <Link to="/" className="pure-menu-link" onClick={this.handleOnCLick}>Home</Link>
                                </li>
                                <li className="pure-menu-item">
                                    <Link className="pure-menu-link" to="/about" onClick={this.handleOnCLick}>Acerca</Link>
                                </li>
                                <li className="pure-menu-item">
                                    <Link className="pure-menu-link" to="/register" onClick={this.handleOnCLick}>Registro</Link>
                                </li>
                                <li className="pure-menu-item">
                                    <Link className="pure-menu-link" to="/login" onClick={this.handleOnCLick}>Login</Link>
                                </li>
                              
                            </ul>
                    
                        </div>
                    </header>
                        <main className="Main">
                            <Switch>
                                <Route path='/' exact component={Home} />
                                <Route path='/about' component={About} />
                                <Route path='/register' component={Register} />
                                <Route path='/login' component={Login} />
                                <Route component={Error404} />
                            </Switch>
                        </main>
                        

                    </div>
                </BrowserRouter>
        )
    }


}

export default App


