import React, { Component } from 'react'
import { login } from '../helpers/Auth'

export default class Login extends Component {
    constructor(...props){
        super(...props)
        this.state = {
            loginMessage: null
        }

        this.handleSubmit = this.handleSubmit.bind(this)
    }

    handleSubmit(event) {
        event.preventDefault()
        login(this.email.value, this.password.value)
            .catch( error => this.setState(this.setErrorMsg('Error ..')))
    }

    setErrorMsg (err) {
        return {loginMessage: err}
    }
    render(){
    	return (
        <article className="Main-container">
            <h1>Seccin de Login</h1>
            <form onSubmit={this.handleSubmit}>
                <input type="email" placeholder="Email" ref={email => this.email= email} />
                <input type="password" placeholder="Password" ref={password => this.password= password}/>
                {
                    this.state.loginMessage &&
                    <div className="error">
                        Error : {this.state.loginMessage}
                    </div>
                }
                  <input type="submit" value="Login"/>

            </form>
          
        </article>
        )
    }
}


